@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
  /* background: rgb(253, 253, 253); */ 
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: rgb(51, 20, 20);
  background-color: aquamarine;
  
}

.App {
  background-color: red;
}

.MuiAppBar-colorPrimary {
  background: linear-gradient(90.96deg, rgba(185, 201, 224, 0.4) 1.05%, rgba(241, 108, 11, 0.13) 99.89%);
  background-color: aquamarine;
}

.hero {
  display: flex;
  width: 80%;
  padding: 0.5em;
  margin-left: 15%;
  margin-right:10%;
  margin-bottom: 0%;
  justify-content:flex-start;
  align-items: center;
  height: 50px;
  background-color: rgb(255, 255, 255);
  margin-bottom: 10px;
}

.select-radio{
  width:100%;
  display: flex;
  flex-direction: row;
}
.select-radio input{
  align-items: flex-end;
  flex:1;
  padding-left: 10px;
}
.select-radio p{
  width:80%;
}


.top-container{
  width:90%;
  height:100px;
  display: flex;
  align-items: left;

}
.btn-1{
  margin-left:15%;
  /* width:10px;
  height:5px; */
  display: flex;
  width:30px;
  align-self: center;
  border :2px solid #054DB9;
  border-radius: 13px;
  font-weight:300px;

}
.section{
  
  display: block;
  justify-content:flex-start;
  align-items: center;
  width: 40%;
  height:20px;
  margin-left: 9.4%;
  margin-right:10%;
  margin-top:10px;
  font-weight: 300;
  margin-bottom: 1%;
  
  background-color: rgb(255, 255, 255);
  font-weight: bolder;
}
.section h1{
  margin-top: 3%;
  margin-left: 15%;
  width:100%;
  font-size: 1.3em;
  padding-bottom:0% 2px 2px 2px;
}
.MuiButton-root {
  color: rgb(228, 201, 201) !important;
}

.devicebtn {
  margin-right: 4em !important;
}
.chart{
  display: flex;
  width: 80%;
  
}
.charttable {
  display: flex; 
  align-items: center;
  justify-content:space-evenly;
  align-items: center;
  margin:140px;
  margin-top: 10px;
  background-color: #fff;
  box-shadow: 5px 10px  #634040 0.5;

}
.ch ,clr h6{
  background-color: #fff;
}
.vl {
    border-left: 2px solid rgba(93, 91, 91, 0.329);
    height: 200px;
}
.child {
  width: 20%;
  background-color: #fff;
}
.child h1 ,.child3 h1 ,.child1 h1{
  display: flex;
  align-items: center;
  font-size:25px;
  background-color: #fff;
}
.child3, .child1{
  width: 20%;
  background-color: #fff;
}


@media (max-width: 700px) {
  .devicebtn {
    display: none !important;
  }
  .hr{
    display:inline;
  }
  .vl {
    display: none;
  }
  .child doughnut{
    background-color: #fff;
  }

  .charttable {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #fff;
  }

  .child {
    margin-left:0%;
    width: 80%;
    margin-top: 3em;
  }

  .child1 {
    
    margin-top: 3em;
    width: 80%;
  }

  .child3 {
    margin-top: 3em;
    width: 80%;
  }
  .chart{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80%;
    margin:30px;
    align-items: center;
    
  }
  .charttable {
    display: flex;
    width: 80%;
    /* justify-content:space-evenly; */
    /* padding-left:0px;*/
    margin-left:10%;
    margin-bottom:10%;
    margin-right: 3%; 
  
  }
  .ch{
    margin-left: 2%;
    display: flex;
    flex-direction: column;
  }
  .c{
    margin-left:0%;
  }
}
